import React, { Fragment } from "react";
import { Row, Col } from "react-bootstrap";
import Fade from "react-reveal/Fade";
export default function Details() {
  return (
    <Fragment>
      <Row
        style={{ marginTop: "150px", marginBottom: "150px",marginLeft:"10px"}}
        className="show-grid justify-center"
      >
        <Col
          lg={6}
          md={6}
          sm={12}
          style={{paddingBottom: "2%" }}
        >
          <Fade>
            <h1>Aniket Raj</h1>
            <h3>A Student of IIT Dhanbad</h3>
            <hr
              style={{ width: "80%", paddingBottom: "2px", paddingTop: "20px" }}
            ></hr>

            <p style={{ fontSize: "18px" }}>Web Developer</p>
          </Fade>
        </Col>

        <Col
          lg={6}
          md={6}
          sm={12}
          style={{paddingBottom: "2%" }}
        >
          <Fade>
            <p style={{ fontSize: "20px" }}>
            <header>Works with <a href="https://en.wikipedia.org/wiki/HTML5" >HTML-5</a>, <a href="https://en.wikipedia.org/wiki/CSS" >CSS-3</a>, <a href="https://en.wikipedia.org/wiki/ECMAScript">JavaScript(ES7)</a>, <a href="https://nodejs.org/">nodejs</a>,non-sql Database(prefferably <a href="https://www.mongodb.com/">MongoDb</a>), <a href="https://expressjs.com/">Express</a>, <a href="https://reactjs.org/" >Reactjs</a>, <a href="https://redux.js.org/" >Redux</a>, backend routing management with <a href="https://www.postman.com/">Postman </a> 
             ,have a good hand on <a href="https://en.wikipedia.org/wiki/API">REST APIs</a> with npm package <a href="https://www.npmjs.com/package/request">'request'</a>.
             Have more than 10+ tremendous projects on front end with reactjs,
             have been in web-team of 3 organisation and had completed their websites satisfactorily.
             </header>
            </p>
          </Fade>
        </Col>

      </Row>
    </Fragment>
  );
}
