import React from "react";
import Img from "../../assets/slideshow/index.jpg";
import Img1 from "../../assets/slideshow/index1.jpg";
import Img2 from "../../assets/slideshow/index2.jpg";
import Img3 from "../../assets/slideshow/index3.jpg";
import Img4 from "../../assets/slideshow/index4.jpg";
import Img5 from "../../assets/slideshow/index5.jpg";
import Img6 from "../../assets/slideshow/index6.png";
import Img7 from "../../assets/slideshow/index7.png";
import Img8 from "../../assets/slideshow/index8.png";
import Img9 from "../../assets/slideshow/index9.png";
import Img10 from "../../assets/slideshow/index10.png";
import OwlCarousel from "react-owl-carousel";
import { Container } from "react-bootstrap";
import { useMediaQuery } from "react-responsive";
import { Fade } from "react-reveal";

const ImageSlider = ({ title }) => {
  const isDesktop = useMediaQuery({
    query: "(min-device-width: 992px)",
  });

  const isTablet = useMediaQuery({
    query: "(min-device-width: 767px)",
  });

  return (
    <div
      className="word-section slider justify-align-center"
      style={{ marginBottom: "150px" }}
    >
      <h1>Some Frontend Shots</h1>
      <Fade cascade>
        <h2 className="mb-5" style={{ flexDirection: "row" }}>
          {title}
        </h2>
      </Fade>
      <Container>
        <Fade cascade>
          <OwlCarousel
            id="carousel"
            className="owl-theme"
            responsiveClass={true}
            items={isDesktop ? 3 : isTablet ? 2 : 1}
            loop={true}
            nav={true}
            navText={["<", ">"]}
            autoplay={true}
            autoplayTimeout={3000}
            smartSpeed={2000}
            dots={false}
            animateIn="ease-in"
          >
            {[{count:1,link:Img1}, {count:2,link:Img2}, {count:3,link:Img3}, {count:4,link:Img4}, {count:5,link:Img5}, {count:6,link:Img6}, {count:7,link:Img7}, {count:9,link:Img9}, {count:10,link:Img10}, {count:8,link:Img8}].map((data, ind) => {
              return (
                <div className="single-blog" key={data.count + ind}>
                  <div className="blog-img" style={{ height: "auto" }}>
                    <img
                      src={data.link}
                      alt="blog-img"
                      style={{ height: "360px", cursor: "pointer", width:"98%", borderRadius: "10%" }}
                    />
                  </div>
                </div>
              );
            })}
          </OwlCarousel>
        </Fade>
      </Container>
    </div>
  );
};

export default ImageSlider;
