import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFacebook, faGithub, faInstagram, faLinkedinIn, fa } from '@fortawesome/free-brands-svg-icons';
import { Fade } from 'react-reveal';

const Footer = ({extraStyle}) => {
    return (
      <div className="word-section footer justify-center" style={extraStyle}>
        <Fade top cascade>
          <div className="left">
            <h2>About me</h2>
            <p className="lead">
              I've completed .{" "}
            </p>
          </div>
        </Fade>
        <Fade top cascade>
        <div className="right" style={{justifyContent:"normal"}}>
          <h3 className="connect-text">Contact Me:</h3>
          <div className="social-links">
            {data.map((item, ind) => {
              const { icon, link, name } = item;
              return (
                <span key={item + ind}>
                  <a
                    href={link}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ paddingLeft: "0" }}
                  >
                    <FontAwesomeIcon icon={icon} className={name} />
                  </a>
                </span>
              );
            })}
          </div>
        </div>
        </Fade>
        <Fade bottom cascade>
        <div className="bottom-div jusitfy-align-center">
          <p className="first-text my-1">Made By Aniket Raj</p>
          <p className="second-text">
            <em>My Portfolio</em>
          </p>
        </div>
        </Fade>
      </div>
    );
}

export default Footer;

const data = [
  {
    icon: faFacebook,
    name: "facebook",
    link: "",
  },
  {
    icon: faInstagram,
    name: "instagram",
    link: "",
  },
  {
    icon: faLinkedinIn,
    name: "linkedin",
    link: "",
  },
  {
    icon: faGithub,
    name: "Github",
    link: "",
  }
];
