import React from "react";
import { Fragment } from "react";
import Fade from "react-reveal/Fade";
export default function First() {
  return (
    <Fragment>
      <div className="box-div" id="home">
        <div className="head mx-5">
          
            <Fade top cascade>
              <header>
                <h1 className="head-div-first-text">
                  <span style={{ fontWeight: 900 }}>Aniket Raj</span>
                </h1>
                {/* <hr
              style={{ width: "80%", paddingBottom: "2px", paddingTop: "20px" }}
            ></hr> */}
                <h4 className="head-div-second-text">
                  A MERN stack Web Developer{" "}
                </h4>
                </header>
            </Fade>
         
        </div>
        <Fade bottom>
          <span className="scroll-btn">
            <a href="#home">
              <span className="mouse">
                <span></span>
              </span>
            </a>
          </span>
        </Fade>
      </div>
    </Fragment>
  );
}
